<template>
  <div class="wrapper">
    <div class="title">我的订单</div>
    <div class="orders">
      <div
        class="order"
        v-for="item in list"
        :key="item.id">
        <div class="order__title">
          {{ item.shopName }}
          <span
            class="order__status"
            v-html="(item.isCanceled ? '已取消' : '已完成')"
          ></span>
        </div>
        <div class="order__content">
          <div class="order__content__imgs">
            <template
              v-for="( innerItem, innerIndex ) in item.products"
              :key="innerItem.id">
              <img
              class="order__content__img"
              :src="innerItem.imgUrl"
              v-if="innerIndex < 4"
            >
            </template>

          </div>
          <div class="order__info">
            <div class="order__info__price">&yen; {{ item.price }}</div>
            <div class="order__info__count">共{{ item.total }}件</div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <Docker :currentIndex="2"/>
</template>

<script>
import { reactive, toRefs } from 'vue'
import Docker from '../../components/Docker.vue'
import { get } from '../../utils/request'
// 处理订单列表逻辑
const useOrderListEffect = () => {
  const data = reactive({ list: [] })
  const getOrderList = async () => {
    const result = await get('/order')
    data.list = result
  }
  const { list } = toRefs(data)
  return { list, getOrderList }
}

export default {
  name: 'OrderListView',
  components: { Docker },
  setup () {
    const { list, getOrderList } = useOrderListEffect()
    getOrderList()
    return { list, getOrderList }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/virables.scss';
.wrapper {
  overflow-y: auto;
  position: absolute;
  left: 0;
  right: 0;
  top:0;
  bottom: .5rem;
  background: #eee;
}
.title{
  line-height: .44rem;
  background: $bgColor;
  font-size: .16rem;
  color:$content-fontColor;
  text-align: center;
}
.orders{
  font-size: .14rem;
}
.order{
  margin: .16rem .18rem;
  padding: .16rem;
  background: $bgColor;
  &__title{
    margin-bottom: .16rem;
    line-height: .22rem;
    font-size: .16rem;
    color:$content-fontColor;
  }
  &__status{
    float: right;
    font-size: .14rem;
    color: $light-fontColor;
  }
  &__content{
    display: flex;
    &__imgs{
      flex: 1;
    }
    &__img{
      width: .4rem;
      height: .4rem;
      margin-right: .12rem;
    }
  }
  &__info{
      width: .7rem;
      justify-content: space-between;
      text-align: right;
      &__price{
        line-height: .2rem;
        font-size: .14rem;
        color: $hightlight-fontColor;
        margin-bottom: .04rem;
      }
      &__count{
        font-size: .12rem;
        color: $content-fontColor;
      }
    }
}
</style>
